.slideshow {
  position: relative;
}

.slideshow--nav {
  color: #ffffff;
  cursor: pointer;
  align-items: center;
  display: flex;
  height: 50px;
  top: 50%;
  margin-top: -26px;
  width: 50px;
  position: absolute;
  transition: all linear 0.3s;
}

.slideshow--nav:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.slideshow--nav .slideshow--nav--prev {
  left: 0;
}

.slideshow--nav .slideshow--nav--next {
  right: 0;
}

.slide {
  position: relative;
}
